.newbuild-radio {
  display: none;
}

.newbuild .type,
.newbuild .catalog-radio {
  display: none;
}

.newbuild .newbuild-radio {
  display: block;
}

.newbuild .filter__input {
  margin-bottom: 20px;
}

.newbuild .filter__title {
  margin-bottom: 14px;
}

.newbuild__grid {
  background: #000 url('../img/nb-bg.jpg') no-repeat center/cover;
}

.newbuild__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.newbuild__item {
  width: 33%;
  position: relative;
  transition: 0.3s;
}

.newbuild__img {
  width: 100%;
  height: 100%;
  margin-bottom: -5px;
}

.newbuild__button {
  font-size: 20px;
  position: absolute;
  padding: 20px;
  bottom: 0;
  left: 0;
  z-index: 2;
  opacity: 0;
}

.newbuild__item:hover,
.newbuild__item:focus {
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border: 4px solid $prime-color;
  }

  & .newbuild__button {
    opacity: 1;
  }
}
