.filter {
  float: left;
  margin-left: 142px;
  background: #212121;
  padding: 10px 0 15px;
  color: #fff;
  width: 286px;
  display: none;
  min-height: 843px;

  &.open {
    display: block;
  }
}

.filter__form {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  min-height: 818px;
}

.filter__input {
  width: 256px;
  height: 47px;
  padding: 15px 10px;
  margin: 0 15px 10px;
  background: rgba(0, 0, 0, 0.38);
  border: none;
  box-shadow: none;
  color: #fff;

  &::placeholder {
    color: rgba(#fff, 0.36);
  }

  &#search {
    background-image: url('../img/lupa-w.png');
    background-repeat: no-repeat;
    background-position: center right 10px;
    padding-right: 35px;
  }
}

#slider-range,
#slider-range2 {
  margin: 0 15px;
  position: relative;
  margin-bottom: 15px;
}

#squere, #amount{
  display: none;
}

.ui-widget-content {
  background: rgba(#fff, 0.5);
  margin-top: 30px;
  height: 3px;
}

.ui-widget-header {
  background: $prime-color;
  height: 3px;
}

.ui-slider .ui-slider-range {
  position: absolute;
  z-index: 1;
  font-size: .7em;
  display: block;
  border: 0;
  background-position: 0 0;
}


.ui-slider-horizontal .ui-slider-range {
  top: 0;
  height: 100%;
}

.ui-slider .ui-slider-handle {
  position: absolute;
  top: -6px;
  z-index: 2;
  width: auto;
  cursor: pointer;
  text-align: center;
  font-size: 0px;
  margin-left: -.6em;
}

span.ui-slider-handle.ui-corner-all.ui-state-default {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: $prime-color;
}

.filter__fieldset {
  padding: 0;
  border: none;

  &.type {
    display: flex;
    margin-bottom: 10px;
  }
}

.type input[type="radio"]:checked,
.type input[type="radio"]:not(:checked) {
  width: 93px;
  height: 98px;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
  margin-right: -93px;
}

.type input[type="radio"]:checked + label,
.type input[type="radio"]:not(:checked) + label {
  display: flex;
  position: relative;
  flex-direction: column-reverse;
  text-align: center;
  padding: 13px 0;
  text-transform: uppercase;
  cursor: pointer;
  font-family: $title-font;
  background-color: #282828;
  font-size: 20px;
  width: 33%;
  transition: 0.3s;

  & svg {
    opacity: 0.25;
    display: block;
    margin: 0 auto 10px;
  }
}

.type input[type="radio"]:checked + label,
.type input[type="radio"]:hover + label {
  color: $text-color;
  background-color: $prime-color;

  & svg {
    opacity: 1;
    fill: $text-color;
  }
}

.filter__title {
  font-size: 12px;
  font-weight: 500;
  opacity: 0.7;
  margin-bottom: 8px;
  margin-left: 15px;
}

.price input[type="radio"]:checked,
.price input[type="radio"]:not(:checked) {
  width: 25px;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
  margin-top: -19px;
  float: right;
  margin-right: 15px;
}

.price input[type="radio"]:checked + label,
.price input[type="radio"]:not(:checked) + label {
  color: #5F5F5F;
  font-size: 12px;
  font-weight: 500;
  transition: 0.3s;
  float: right;
  margin: -20px 15px 15px 0;
}

.price input[type="radio"]:checked + label,
.price input[type="radio"]:hover + label {
  color: $prime-color;
  text-decoration: underline;
}

.filter__half {
  display: flex;
  justify-content: space-between;
  margin: 0 15px 10px;
  clear: both;
}

.filter__input--half {
  margin: 0;
  width: 48%;
  padding-left: 30px;
  position: relative;

  &::before {
    font-family: inherit;
    color: #FFFFFF;
    opacity: 0.25;
    position: absolute;
    top: 5px;
    left: 5px;
  }

  &#price-min::before,
  &#squere-min::before {
    content: "От";
  }

  &#price-max::before,
  &#squere-max::before {
    content: "До";
  }
}

.filter__radio input[type="radio"]:checked,
.filter__radio input[type="radio"]:not(:checked) {
  width: 92px;
  height: 26px;
  opacity: 0;
  position: absolute;
  z-index: 1;
  cursor: pointer;
}

.filter__radio input[type="radio"]:checked + label,
.filter__radio input[type="radio"]:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  margin-bottom: 35px;
  margin-left: 11px;
  line-height: 26px;
}

.filter__radio input[type="radio"]:checked + label:before,
.filter__radio input[type="radio"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 26px;
  height: 26px;
  border: 3px solid #595959;;
}

.filter__radio input[type="radio"]:checked + label:before {
  border-color: $prime-color;
}

.filter__radio input[type="radio"]:checked + label:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 7px;
  width: 12px;
  height: 12px;
  background-color: $prime-color;
}

@media (max-width: 1180px) {
  .filter {
    float: none;
    width: calc(100% - 247px);
    margin-bottom: 30px;
  }
}

@media (max-width: $mobile-width) {
  .filter.open {
    display: none;
  }

  .filter {
    position: absolute;
    top: 247px;
    left: 0;
    width: 100%;
    margin: 0;
    min-height: auto;
  }
}
